<template>
    <div>
      <el-container>
        <el-header class="home-header" height="48px">
          <div>
            <span class="home_title">DevOps Platform</span>
          </div>
          <div>
            <el-dropdown style="color: black" @command="handleCommand">
            <span class="el-dropdown-link home_userinfo" style="display: flex;align-items: center; vertical-align: middle;">
              {{user.name}}
              <i><img v-if="user.userface!=''" :src="user.userface"
                      style="width: 20px;height: 20px;margin-right: 5px;margin-left: 5px;border-radius: 40px; background-color:#D3DCE6 "/></i>
            </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item><a href="/#/profile">Account Center</a></el-dropdown-item>
                <el-dropdown-item command="logout" divided>Log Out</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-header>
        <el-main class="setting-profile-main">
          <el-row>
            <el-col :span="10">
              <div class="profile-left">
                <img :src="user.userface" class="profile-avatar">
                <div class="profile-name">
                  <div class="profile-username">{{user.name}}</div>
                  <button class="profile-button" @click="editClick">编辑信息</button>
                </div>
                <div class="profile-notice">
                  <i class="el-icon-warning"></i>
                  <span class="profile-notice-text">The following need to set up carefully!</span>
                </div>
                <div class="profile-info">
                  <el-tooltip class="item" effect="light" content="Your RepoName" placement="bottom-start">
                    <i class="el-icon-s-shop profile-icon"></i>
                  </el-tooltip>
                  <div class="profile-detail">{{user.githubName}}</div>
                </div>
                <div class="profile-info">
                  <el-tooltip class="item" effect="light" content="Your Github Token" placement="bottom-start">
                  <i class="el-icon-s-ticket profile-icon"></i>
                  </el-tooltip>
                  <div class="profile-detail">{{user.githubToken}}</div>
                </div>
                <br>
                <div class="profile-notice">
                  <i class="el-icon-ice-drink light-color"></i>
                  <span class="profile-notice-text">Basic Information</span>
                </div>
                <div class="profile-info">
                  <i class="el-icon-message profile-icon light-color"></i>
                  <div class="profile-detail">{{user.email}}</div>
                </div>
                <div class="profile-info">
                  <i class="el-icon-house profile-icon light-color"></i>
                  <div class="profile-detail">{{user.address}}</div>
                </div>
                <div class="profile-info">
                  <i class="el-icon-mobile-phone profile-icon light-color"></i>
                  <div class="profile-detail">{{user.phone}}</div>
                </div>
              </div>
            </el-col>
            <el-col :span="14">
              <div class="profile-right">

              </div>
            </el-col>
          </el-row>
        </el-main>
        <footer class="home-footer">
          <span class="foot_title">© 1999-2019 Citrix Systems, Inc. 保留所有权利。</span>
        </footer>
      </el-container>
    </div>
</template>

<script>
    export default {
      methods: {
        handleCommand(cmd) {
          var _this = this;
          if (cmd == 'logout') {
            this.$confirm('Are you sure to log out?', 'Hint', {
              confirmButtonText: 'Confirm',
              cancelButtonText: 'Cancel',
              type: 'warning'
            }).then(() => {
              _this.getRequest("/logout");
              _this.$store.commit('logout');
              _this.$router.replace({path: '/'});
            }).catch(() => {
              _this.$message({
                type: 'info',
                message: 'Cancel'
              });
            });
          }
        },
        editClick: function () {
          this.$router.push("/setting");
        }
      },
        name: "GithubRegister",
      computed: {
        user() {
          return this.$store.state.user;
        }
      }
    }

</script>

<style scoped>
  .profile-left {
    border-right: 1px solid gray;
    height: 700px;
    padding-left: 100px;
    padding-right: 50px;
  }
  .profile-right {
    border: 1px solid blue;
    height: 100px;
  }
  .profile-avatar {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    margin-top: 40px;
    box-shadow: 0 0 2px gray;
  }
  .profile-name {
    display: block;
    height: 40px;
  }
  .profile-username {
    font-size: 24px;
    line-height: 28px;
    font-weight: 400;
    float: left;
  }
  .profile-button {
    float: right;
    border: none;
    font-size: 14px;
    line-height: 18px;
    font-weight: bold;
    padding: 6px 12px;
    background-color: rgba(0,120,212,1);
    color: white;
    border-radius: 2px;
    bottom: 0px;
  }
  .profile-notice {
    text-align: left;
    margin: 10px 0;
    padding: 6px 4px;
    border-bottom: 1px solid rgba(0,120,212,1);
  }
  .el-icon-warning {
    color: rgba(0,120,212,1);
    font-size: 22px;
  }
  .profile-notice-text {
    font-size: 18px;
  }
  .profile-info {
    display: block;
    text-align: left;
    margin: 10px 0;
    padding: 6px 0;
    height: 30px;
  }
  .profile-icon {
    font-size: 24px;
    float: left;
    padding: 4px 2px;
    color: blueviolet;
  }
  .profile-detail {
    float: left;
    padding: 4px 2px;
    margin-left: 4px;
  }
  .light-color {
    color: rgba(0,120,212,1);
  }
  .el-icon-ice-drink {
    font-size: 22px;
  }
</style>



